class InfiniteLoading {
  constructor (opts) {
    this.$loader = opts.loader;
    this.page = opts.page;
    this.pattern = opts.pagePattern;
    this.total = opts.pagesCount;
    this.url = opts.url;
    this.loading = false;
    this.handler = this.handler.bind(this);
    this.onComplete = opts.onComplete;
  }

  disconnect () {
    this.observer.disconnect();
  }

  observe () {
    this.observer =
      new IntersectionObserver(this.handler, { threshold: [0.75] });

    this.$loader.removeAttribute("hidden");

    this.observer.observe(this.$loader);
  }

  handler (targets) {
    if (this.loading) {
      return;
    }

    if (this.page === this.total) {
      this.disconnect();
      this.$loader.remove();

      if (typeof this.onComplete == "function") {
        this.onComplete();
      }

      return;
    }

    if (targets[0].intersectionRatio > 0) {
      this.loading = true;

      this.load()
        .finally(
          () => {
            this.loading = false;
          }
        );
    }
  }

  load () {
    const url = this.pattern.replace(":page", this.page + 1);

    return fetch(url)
      .then(
        (res) => {
          if (res.ok) {
            return res.text();
          }
        }
      )
      .then(
        (markup) => {
          this.page += 1;
          const $lastArticle = this.$loader.previousElementSibling;
          $lastArticle.after(
            ...toNodes(markup)
          );
        }
      );
  }
}

export default InfiniteLoading;

const toNodes =
  (markup) => {
    const parser = new DOMParser();

    const doc = parser.parseFromString(markup, "text/html");

    return doc.querySelectorAll("[data-js='post-list'] > article");
  }
