import InfiniteLoading from "./deps/infinite-loading";

(function Main (BrickJS) {
  const $loader = BrickJS.$("[data-js='loader']").first();

  if ($loader) {
    const observer = new InfiniteLoading({
      ...BrickJS.config.infiniteScrolling,
      loader: $loader,
      onComplete () {
        BrickJS.$("[data-js='pagination']")
          .forEach(
            ($el) => {
              $el.hidden = true;
            }
          )
      },
    });

    observer.observe();
  }
})(window.BrickJS);
